h1 {
  color: red; }

html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  color: #1d1613;
  background-color: #e5e5e5;
  font-family: "Poppins", "Arial", sans-serif;
  font-weight: 500;
  font-size: 16px;
  font-style: normal;
  line-height: 21px; }

a {
  text-decoration: none;
  color: #1d1613; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0 0 0 0);
  border: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%); }

.button {
  color: #ffffff;
  background-color: #a87b62;
  text-align: center;
  border: none;
  cursor: pointer; }

.page-header {
  font-family: "Poppins", "Arial", sans-serif; }

.catalog {
  font-family: "Poppins", "Arial", sans-serif; }

.main-nav {
  font-family: "Poppins", "Arial", sans-serif; }

.social {
  font-family: "Poppins", "Arial", sans-serif; }

.page-footer {
  font-family: "Poppins", "Arial", sans-serif; }
